@import "utils";

.header {
  height: 80px;

  &__container {
    height: 100%;
    display: flex;
    align-items: center;
  }
  &__location {
    @include button(60px, "/images/Checkin.svg");
    &-form {
      position: relative;
      width: 350px;
    }
    input {
      box-sizing: border-box;

      position: absolute;
      left: 0;

      height: 100%;
      width: 100%;

      padding: 0 0 0 46px;

      font-size: 16px;
      color: $text-color;
    }
  }
  &__delivery {
    @include button(40px, "/images/Time.svg");
  }
  &__search {
    @include button(auto, "/images/Loop.svg");
  }
  &__sign-in {
    text-decoration: none;
    color: $text-color;
    margin-left: 40px;
  }
}
